* {
  box-sizing: border-box;
  -webkit-tap-highlight-color:  transparent;
}

.page{
  margin: 0;
  padding: 0;
  .dekstop_page{
    overflow: hidden; 
    --swiper-theme-color: #007aff;
    .header_page{
      align-items: center;
      background-color: #fff;
      display: flex;
      flex-direction: column;
      font-family: Plus Jakarta Sans;
      padding: 0 106px;
      .first_header{
        border-bottom: 1px solid #000;
        align-items: center;
        box-sizing: border-box;
        display: flex;
        height: 100px;
        justify-content: space-between;
        width: 100%;
        .image_first_header{
          cursor: pointer;
          height: 44px;
          width: 106px; 
          border-style: none;
          vertical-align: middle;
        }
        .nav{
          display: flex;
          margin: 0 40px;
          overflow-x: auto;
          padding: 0;
          .nav_item{
            color: #00000080;
            cursor: pointer;
            flex-shrink: 0;
            font-size: 18px;
            margin-right: 41px;
            padding: 6px 0;
          }
          .link_nav{
            border-bottom: 1px solid #000;
            color: #000;
             cursor: pointer;
            flex-shrink: 0;
            font-size: 18px;
            margin-right: 41px;
            padding: 6px 0; 
          }
        }
        .language{
          align-items: center;
          display: flex;
          .language_item1{
            color: #000;
            cursor: pointer;
            flex-shrink: 0;
            font-size: 18px;
            margin-right: 10px;
          }
          .language_item2{
            color: #00000080;
            cursor: pointer;
            flex-shrink: 0;
            font-size: 18px;
            margin-right: 10px;
          }
        }
      }
      .second_header{
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        padding-bottom: 15px;
        padding-top: 15px;
        width: 100%;
        z-index: 10;
        .header_content{
          display: flex;
          justify-content: space-between;
          width: 100%; 
          .second_image{
            cursor: pointer;
            height: 45px;
            width: 134px;
            border-style: none;
            vertical-align: middle;
          }
          .right_content{
            display: flex;
            .button1{
              align-items: center;
              border: 2px solid #2a95ff;
              border-radius: 8px;
              color: #2a95ff;
              cursor: pointer;
              display: flex;
              font-size: 14px;
              font-weight: 700;
              height: 48px;
              justify-content: center;
              margin-right: 10px;
              position: relative;
              width: 134px;
              border: 2px solid #333!important;
              color: #000!important;
              font-weight: 700!important;
            }
            .button2{
              margin-right: 0;
              align-items: center;
              border-radius: 8px;
              cursor: pointer;
              display: flex;
              font-size: 14px;
              height: 48px;
              justify-content: center;
              position: relative;
              width: 134px;
              background-color: #ffd633!important;
              border: 2px solid #ffd633!important;
              color: #000!important;
              font-weight: 700!important;
            }
